$negro: black;
$blanco: #f8f8f8;
$perla: #E1E1E1;
$marron: #5b5852;
$verde: #54b589;
$salmon: #F97E76;
$gris: #7c7b79;
$gris-dot: rgb(187, 187, 187);
$gris-anubis: #323A45;

//GRADIENTS//
